    padding: .5rem;
    resize: none;
  }
  input[type=submit] {
    background: $dark-gray;
    border: none;
    color: #fff;
    cursor: pointer;
    font-family: 'Overpass Mono';
    font-size: 1.3rem;
    margin: 1rem 0;
    padding: .4rem;
    &:active {
      background: #ccc;
    }
